export default {
	data() {
		return {
			//把接受过来的值重新赋值一下
			isguanzhu:this.item.isguanzhu,
			infonum:this.item.infonum,
			type:this.item.type
			
		};
	},
	//接受父组件传过来的值
	props:{
		item:Object,
		index:Number
	},
	methods:{
		//关注事件
		guanzhu(){
			this.isguanzhu=true;
			uni.showToast({
				title:"关注成功!"
			})
		},
		//顶踩
		caozuo(type){
			// infonum: {
			// 	index: 2, //0:没有,1:顶,2:踩
			// 	dingnum: 12,
			// 	cainum: 14
			// },
			switch (type){
				case "ding":
				if(this.item.infonum.index==0){
					this.item.infonum.index=1;
					this.item.infonum.dingnum++;
				}
				else if(this.infonum.index==1){
					this.infonum.index=0;
					this.infonum.dingnum--;
				}
				else if(this.infonum.index==2){
					this.infonum.index=1;
					this.infonum.dingnum++;
					this.infonum.cainum--;
				}
				break;
				case "cai":
				if(this.infonum.index==0){
					this.infonum.index=2;
					this.infonum.cainum++;
				}
				else if(this.infonum.index==2){
					this.infonum.index=0;
					this.infonum.cainum--;
				}
				else if(this.infonum.index==1){
					this.infonum.index=2;
					this.infonum.cainum++;
					this.infonum.dingnum--;
				}
				break;
			}
		},
		opendetail(){
			console.log("进入详情")
		}
	}
}
